<template>
<div>
    <div class="pagebar-title">新增或编辑</div>

    <div class="module">
      <div class="mtitle">接口名</div>
      <el-input v-model="form.name" placeholder="接口名"></el-input>
    </div>

    <div class="module">
      <div class="mtitle">接口所属归档</div>
    <el-radio-group v-model="form.type">
      <el-radio-button label="1">农产品分类管理接口</el-radio-button>
      <el-radio-button label="2">主体管理</el-radio-button>
      <el-radio-button label="3">主体下农产品管理</el-radio-button>
      <el-radio-button label="4">采集数据调取接口</el-radio-button>
      <el-radio-button label="5">设备从机和指令参数接口</el-radio-button>
      <el-radio-button label="6">网关设备接口</el-radio-button>
      <el-radio-button label="7">设备回调参数模板管理接口</el-radio-button>
    </el-radio-group>
    </div>

    <div class="module">
      <div class="mtitle">接口地址</div>
      <el-input v-model="form.url" placeholder="接口地址"></el-input>
    </div>

    <div class="module">
      <div class="mtitle">请求Json报文示例</div>
      <!-- <el-input v-model="form.json" placeholder="Json报文"></el-input> -->
      <el-input
        type="textarea"
        placeholder="请输入内容"
        v-model="form.putjison">
      </el-input>
    </div>

    <div class="module">
      <div class="mtitle">接口说明</div>
        <editor
          v-model="form.doc"
          :content="form.doc"
          class="editor"
        />
    </div>

    <div class="module">
      <div class="mtitle">返回Json报文示例</div>
      <!-- <el-input v-model="form.json" placeholder="Json报文"></el-input> -->
      <el-input
        type="textarea"
        placeholder="请输入内容"
        v-model="form.outjson">
      </el-input>
    </div>

    <div class="module">
      <div class="mtitle">确认无误后点击提交</div>
      <el-button @click="push">提交</el-button>
    </div>

</div>
</template>

<script>
import { Addandedit, Getdata } from "@/api/opendosc";
import Editor from "@/components/Editor.vue";
export default {
  name: 'Page404',
  data() {
    return {
        form: {
            id:null,
            type:null,
            name:null,
            url:null,
            doc:null,
            putjison:null,
            outjson:null
        }
    }
  },
  components:{
    Editor
  },
  methods:{
    push() {
      this.$http(Addandedit(this.form), (res) => {
        console.log(res);
        if (res.code == 200) {
          this.$message({
            message: res.msg,
            duration: 5 * 1000,
          });
        } else {
          this.$message({
            message: res.msg,
            type: "error",
            duration: 5 * 1000,
          });
        }
      });
    },
    getDocs(id){
      this.$http(Getdata({
        id:id
      }), (res) => {
        console.log(res);
        if (res.code == 200) {
          this.$message({
            message: res.msg,
            duration: 5 * 1000,
          });
          let data = res.data
          this.form.id = data.id
          this.form.type = data.type
          this.form.name = data.name
          this.form.url = data.url
          this.form.doc = data.doc
          this.form.putjison = data.putjison
          this.form.outjson = data.outjson
        } else {
          this.$message({
            message: res.msg,
            type: "error",
            duration: 5 * 1000,
          });
        }
      });
    }
  },
  created() {
    let id = this.$route.query.docid
    if (id){
      this.form.id = id;
      this.getDocs(id)
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
</style>
